import dnIcon from '@/components/dn-icon/dn-icon.vue'

export default {
	props: ['list', 'arr'], //数组  arr
	components: {
		dnIcon,
	},
	data() {
		return {
			i1: null,
			i2: null,
			show: false,
			listchild: [],
			newlist: this.list
		}
	},
	methods: {
		alertnum(i) {
			if (this.i1 != i) {
				this.listchild = [];
				this.i1 = i;
				this.listchild = this.arr[i];
				this.i2 = null;
				this.show = true;
				let ins = this.listchild.indexOf(this.newlist[i].name);
				if (ins > -1) {
					this.i2 = ins
				}
			}

		},
		chooseOne(i) {
			this.i2 = i;
			this.newlist[this.i1].name = this.listchild[i];
			this.$emit('chooseLike', [this.i1, this.i2])
			this.hide()
		},
		hide() {
			this.show = false;
			this.i1 = null;
			this.i2 = null;
		}
	}
}
